import React from 'react';
import {frontend} from '../Single/Single';
import {Link} from 'react-router-dom';


function PortfolioHomeFront(){
    const prop = frontend
    return(
        <div className="container">
            <div className="row">
                {prop.map((i => (
                    <React.Fragment key={i.id}>
                        <Link to={{
                            pathname:`/development/${i.name}`,
                            state:i.color
                        }}>
                            <div className="col-12 special">
                                <div className="portfolio-home">
                                    <img className="portfolio-home-image" src={i.image} alt="menu-icon" />
                                </div>
                            </div>
                        </Link>
                    </React.Fragment>
                ))).reverse()}
            </div>
        </div>
    )
}
export default PortfolioHomeFront;